/*checkbox样式*/
.checkboxThree {
  width: .48rem;
  height: .16rem;
  background: #333;
  border-radius: .2rem;
  position: relative;
  display: inline-block;
}
.checkboxThree:before {
  content: 'On';
  position: absolute;
  top: .048rem;
  left: .052rem;
  height: .008rem;
  color: #26ca28;
  font-size: 0rem;
}
.checkboxThree:after {
  content: 'Off';
  position: absolute;
  top: .048rem;
  left: .336rem;
  height: .008rem;
  color: #ddd;
  font-size: 0rem;
}
.checkboxThree label {
  display: block;
  width: .208rem;
  height: .088rem;
  border-radius: .2rem;
 
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  cursor: pointer;
  position: absolute;
  top: .036rem;
  z-index: 1;
  left: .048rem;
  background: #ddd;
}
.checkboxThree label.active{
  left: .24rem;
  background: #26ca28;
}
.checkboxThreeInput{
  visibility: hidden;
  margin: 0rem;
}
